<template>
  <div id="app" >

    <div class="nav-container" v-if="isMobile">
      <div class="menu-burger" @click='openNavbar'>
        <div class="burger-line " :class="{'line-1': navbarOpen}"></div>
        <div class="burger-line " :class="{'line-2': navbarOpen}"></div>
        <div class="burger-line " :class="{'line-3': navbarOpen}"></div>
      </div>
    </div>

    <div id="nav" :class="{'nav-mobile': isMobile, 'nav': !isMobile, 'hiddenNav': hideDesktopNavbar, 'nav-background': backgroundForNavbar}" v-if="!isMobile" >
      <div class="emblem-wrap-center">
          <img src="./assets/images/garden-emblem.png" alt="">
      </div>
      <div class="nav-desktop-left">
        <router-link to="/">DISCOVER</router-link>
        <router-link to="/about">ABOUT US</router-link>
        <a href="#">ACCESSORIES</a>
      </div>
      <div class="nav-desktop-right">
        <a href="#shop" class="last">SHOP</a>
      </div>
    </div>

    <transition name="menu-transition">
      <div class="nav-mobile" v-if="navbarOpen && isMobile">
        <router-link to="/" @click.native="closeMobileNav('top')">DISCOVER</router-link>
        <router-link to="/about" @click.native="closeMobileNav('top')">ABOUT US</router-link>
        <a href="#accessories" @click="closeMobileNav">ACCESSORIES</a>
        <a href="#shop" class="last" @click="closeMobileNav">SHOP</a>
      </div>
    </transition>
      

      
    
    <router-view class="main-view"></router-view>

    <div class="footer-grid">
          <div class="emblem-wrap">
            <img src="./assets/images/garden-emblem.png" alt="">
          </div>

          <ul class="footer-list">
            <li class="footer-list-item">Contact Us</li>
            <li class="footer-list-item"> Delivery</li>
            <li class="footer-list-item">Refunds and Returns</li>
            <li class="footer-list-item">Privacy Policy</li>
            <li class="footer-list-item">Terms and Conditions</li>
          </ul>

          <div class="social-media-wrap">
            <i class="fab fa-facebook fa-2x"></i>
            <i class="fab fa-instagram fa-2x"></i>
            <i class="fab fa-twitter fa-2x"></i>
          </div>
          
        </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      windowWidth: window.innerWidth,
      navbarOpen: false,
      prevScrollPosition: window.pageYOffset,
      hideDesktopNavbar: false,
      initialScrollPosition: window.pageYOffset,
    }
  },
  methods: {
    openNavbar() {
      this.navbarOpen = !this.navbarOpen;
    },
    hideNavbar() {
      let currentScrollPosition = window.pageYOffset;
      if (this.prevScrollPosition > currentScrollPosition) {
        this.hideDesktopNavbar = false;
      } else {
        this.hideDesktopNavbar = true;
      }
      this.prevScrollPosition = currentScrollPosition;
    },
    closeMobileNav(scroll) {
      if (scroll = 'top') {
        window.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth'
        });
      }
      this.navbarOpen = false;
    }
  },
  computed: {

    isMobile() {
      if (this.windowWidth > 1000) {
        return false;
      } else {
        return true;
      }
    },
    backgroundForNavbar() {
      if (this.prevScrollPosition > this.initialScrollPosition) {
        return true;
      } else {return false;}
    }

  },
  mounted() {
    window.addEventListener('resize', () => {
      this.windowWidth = window.innerWidth
      
      
    })
  },
  created() {
    window.addEventListener('scroll', this.hideNavbar);
  },
  destroyed() {
    window.removeEventListener('scroll', this.hideNavbar);
  }
}
</script>


